input[type="checkbox"].m-input+label:before, label.check-active+label:before, label.check-selected+label:before, label.check-inactive+label:before {
  content: '';
  display: block;
  width: 15px;
  height: 15px;
  border: 1px solid #000;
  position: absolute;
  left: 0px;
  top: 0px;
  opacity: 1;
  -webkit-transition: all .12s, border-color .08s;
